.AppStripe {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(41, 41, 51, 0.9);
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  z-index: 5000;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  animation: load 0.18s;
  margin: 0px;
}

@keyframes load {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.close {
  position: absolute;
  top: 30px;
  right: 30px;
  align-self: flex-end;
  margin: 5px;
  height: 40px;
  width: 40px;
  border: 2px solid #fa755a;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  border-radius: 7px;
  font-size: 1.8em;
  font-weight: bolder;
  cursor: pointer;
  color: #8a948a;
}
.close:hover {
  box-shadow: 0px 0px 8px #8a948a;
}

form {
  position: relative;
  width: 40vw;
  box-shadow: 0px 0px 0px 0.5px rgba(50, 50, 93, 0.1),
    0px 2px 5px 0px rgba(50, 50, 93, 0.1), 0px 1px 1.5px 0px rgba(0, 0, 0, 0.07);
  border-radius: 7px;
  padding: 20px 20px;
  background-color: white;
  width: 500px;
  min-width: 290px;
}
